input {
    border: 3px solid rgb(155, 148, 148);
    padding: 10px 18px;
    border-radius: 15px;
    font-size: 20px;
    margin-bottom: 5px;
}

.login__header {
    margin-bottom: 20px;
    font-size: 50px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.submit__btn button{
    transition: .3s;
    padding: 10px 30px;
    border-radius: 15px;
    background-color: greenyellow;
}
.submit__btn button[disabled]{
    color: #000;
    background-color:gray;
}