.header__container{
    grid-area: header;
    padding: 10px 20px;
    border: 3px solid rgb(183, 179, 179);
}
.header__logo{
    width: 50px;
}
.header__logo img{
    max-width: 100%;
}
.header__content{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header__profile{
    width: 200px;
}
.profile__content{
    display: flex;
    align-items: center;
}
.profile__img{
    max-width: 50px;
    margin-right: 10px;
}
.profile__img img{
    
    width: 100%;
    border-radius: 50%;
}