.user__message{
    margin-bottom: 20px;
}
.message__author{
    margin-bottom: 10px;
}
.message__container{
    padding: 15px 25px;
    border-radius: 20px;
    border: 2px solid gray;
    max-width: 500px;
}

.message__field textarea{
    margin-right: 20px;
    border: 2px solid gray;
    width: 100%;
    border-radius: 10px;
    padding: 10px;

}
.message__field{
    display: flex;
    
}
.message__field button{
    transition: .3s;
    border-radius: 10px;
    padding: 10px;

}
.message__field button:hover{
    color: #fff;
    background-color: rgb(19, 157, 19);
}
