.userProfile__container{
    padding: 0 20px 0 10px;
    
}
.userProfile__generalInfo{
    display: flex;
    margin-top: 30px;
}
.userProfile__info{
    margin-top: 30px;
    margin-left: 30px;
}
.userProfile__name{
    font-size: 50px;
}
.userProfile__background{
    width: 100%;
}
.userProfile__background img{
    border-radius: 0 0 10px 10px;
    max-width: 100%;
}
.userProfile__avatar img{
    width: 150px;
    border-radius: 50%;
}
.userProfile__status{
    margin-top: 10px;
    font-size: 20px;
}