.nav__container {
    padding: 10px;
    grid-area: navbar;
    border: 3px solid rgb(183, 179, 179);
}

/* .nav__elements{} */
.nav__element {
    margin-bottom: 10px;
    text-align: center;
}
.nav__element a{
    transition: .3s;
    position: relative;
    color: gray;
    font-size: 20px;

}
.nav__element a.active{
    color: black;
}
.nav__element a:after{
    position: absolute;
    content: '';
    width: 0;
    height: 2px;
    background-color: red;
    bottom: -5px;
    left: 0;
    transition: .3s;
}
.nav__element a:hover:after{
    width: 100%;
    
} 
.nav__element a:hover{
    color: #000;
}

